.app-main {
  border-bottom: 2px solid rgb(136, 136, 136);
  -webkit-box-shadow: 2px 3px 1px 1px rgba(138, 138, 138, 0.3);
          box-shadow: 2px 3px 1px 1px rgba(138, 138, 138, 0.3);
  /* background-image: url('../../images/sos.png'); */
  position: fixed;
  z-index: 1000;
  background-color: #fff;
  min-height: 100px;
  /* margin-bottom: 4rem; */
}

div.h1 {
  width: 0px;
  height: 0px;
}

ul {
  list-style: none;
  margin-top: 3%;
}

a {
  color: rgb(90,90,90);
  font-size: 2rem;
  background-color: #fff;
  /* font-family: 'Fjalla One', cursive; */
  font-family: 'Poppins', sans-serif;
}

a:hover, #shop-cart:hover {
  color: rgb(224, 60, 49);
  text-decoration: none;
}

.nav-pills>li>a:hover {
  background-color: #fff;
}

.app-link {
  /* max-width: 25%; */
}

@media only screen and (max-width: 768px) {
  .nav>li {
    /* max-width: 25%; */
  }
  .nav>li>a {
    padding: 0px;
  }
}




